@import '../untils/variables';

.fieldset {
  position: relative;
  display: flex;
  flex-direction: column;

  & + & {
    margin-top: 10px;
  }

  &__title {
    padding-bottom: 5px;
    padding-left: 15px;
    font-size: 17px;
  }

  &__elem {
    display: flex;

    &--container {
      position: relative;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    gap: 5px;
    justify-content: space-between;
  }
}
