@import '../untils/variables.scss';

.logbook {
  display: grid;
  grid-template-columns: 300px 2fr 750px;
  grid-template-rows: auto 1fr;
  min-height: 100vh;
  background: $logbook-bg;

  &__filter {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  &__calendar {
    grid-column: 2/4;
    grid-row: 1/2;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 850px;
    margin: 10px;
  }

  &__table {
    grid-column: 1/4;
    grid-row: 2/3;
    position: relative;
    z-index: 1;
    margin: 10px;
  }

  &__info {
    grid-column: 3/4;
    grid-row: 1/3;
    position: relative;
    z-index: 2;
    display: none;

    &--show {
      display: flex;
    }
  }
}

@keyframes opacityChange {
  0%,
  100% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
}

.disabled {
  pointer-events: none;
  opacity: 0.6;
  background: $button-border-color;
  cursor: none;
}
