@import '../untils/variables';

.form-input {
  padding: 4px 10px;
  font-family: $general-font;
  font-size: 15px;
  line-height: 26px;
  background: $form-input-background;
  border: 1px solid $form-input-border-color;
  border-radius: 6px;

  &[readonly] {
    background: $form-input-background-readonly;
  }

  &:hover {
    border-color: $form-input-border-color-hover;

    &[readonly] {
      background: $form-input-background-readonly-hover;
      border-color: $form-input-border-color;
    }
  }

  &:focus {
    border-color: $form-input-border-color-focus;
    box-shadow: 0 0 0 2px $form-input-outline-color-focus;
    outline: none;

    &[readonly] {
      background: $form-input-background-readonly-focus;
      border-color: $form-input-border-color;
      box-shadow: none;
    }
  }

  &--customer {
    flex-grow: 1;
  }

  &--textarea {
    width: 100%;
    resize: vertical;
  }
}
