@import '../untils/variables';

.button {
  margin: 0;
  padding: 10px;
  color: $button-color;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  font-family: $general-font;
  border: 1px solid $button-border-color;
  border-radius: 6px;
  background: $button-background-color;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  outline: none;
  cursor: pointer;

  & svg {
    pointer-events: none;
  }

  &:hover {
    color: $button-color-hover;
    background: $button-background-color-hover;
  }

  &:focus {
    border-color: $form-input-border-color-focus;
    box-shadow: 0 0 0 2px $form-input-outline-color-focus;
    outline: none;
  }

  &:active {
    opacity: 0.3;
  }

  &--form-action {
    flex-grow: 1;
    min-width: calc(50% - 20px);
    margin: 0 10px;
  }

  &--credit-management {
    margin-right: 10px;
    padding: 8px 10px;
  }

  &--customer-clear {
    position: absolute;
    right: 6px;
    top: 9px;
    padding: 2px;
    background: none;
    border: none;

    & > svg {
      display: block;
    }
  }

  &--mini {
    padding: 2px 10px;
    font-size: 13px;
  }
}
