@import '../untils/variables';

.table {
  display: table;
  border-collapse: collapse;
  width: 100%;
  table-layout: fixed;

  &__row {
    &:hover {
      background: $table-row-background-color-hover;
    }
  }

  &__cell {
    padding: 5px;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    border: 1px solid $table-border-color;

    &--playground {
      width: 170px;
      font-weight: 500;
      text-align: start;
      font-size: 17px;
      background: $table-playground-bg;
    }

    &--post {
      width: 170px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: start;

      &:hover {
        overflow: visible;
      }
    }

    &--time {
      min-width: 58px;
      color: $table-time-color;
      background: $table-time-background-color;
      border-color: $table-time-border-color;
    }
  }
}
